.header{
    background-color:#E07BE0;
    color: white;
    display: flex;
    justify-content: center;
    position: relative;
}

.headerContainer{
    width: 100%;
    max-width: 1024px;
    margin: 20px 0px 100px 0px;
}

.headerContainer.listMode{
    margin: 20px 0px 50px 0px;
}

.headerList{
    display: flex;
    gap: 40px;
}

.headerListItem{
    display: flex;
    align-items: center;
    gap: 10px;
}

.headerListItem.active{
    border: 1px solid #932F6D;
    padding: 10px;
    border-radius: 20px;
}

.headerDesc{
    margin: 20px 0px;
}

.headerBtn{
    background-color:#932F6D;
    color: white;
    border: none;
    padding: 12px 5px;
    border-radius: 10px;
    font-weight: 600;
    cursor: pointer;
}

.headerSearch{
    height: 30px;
    background-color: white;
    border: 3px solid #932F6D;
    display: flex;
    align-items: center ;
    justify-content: space-around;
    padding:10px 0px;
    border-radius: 10px;
    position: absolute;
    bottom: -25px;
    width: 100%;
    max-width: 1024px;
}

.headerIcon{
    color:lightgray
}

.headerSearchItem{
    display: flex;
    align-items: center;
    gap: 10px;
}

.headerSearchInput{
    border: none;
    outline: none;
}

.headerSearchText{
    color:lightgray;
    cursor: pointer;
}

.date{
    position: absolute;
    top: 50px;
    z-index: 2;
}

.options{
    position: absolute;
    top: 50px;
    background-color: white;
    color: gray;
    border-radius: 10px;
    box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.75);   
    z-index: 2; 
}

.optionItem{
    width: 200px;
    display: flex;
    justify-content: space-between;
    margin: 10px;
    align-items: center;
}

.optionCounter{
    display: flex;
    justify-content: center;
    gap: 10px;
    font-size: 13px;
    align-items: center;
}

.optionCounterButton{
    width: 30px;
    height: 30px;
    border: 1px solid #932F6D;
    background-color: white;
    color: rgb(238, 154, 168);
    cursor: pointer;
}

.optionCounterButton:disabled{
    cursor: not-allowed;
}